<template>
    <div class='Comment'>
        <div class="blog-comment">
            <div class="comment-list">
                <ul>
                    <li v-for="(item,index) in commentList" :key="index" @click='goToBlog(item)' style="cursor:pointer">
                        <div class="list-header clearfix">
                            <div class="header-reply">
                                <span>{{item.username}}:</span>
                                <span>{{item.like_count}}</span>
                                <img src='~assets/img/like.svg' @click='handleLike(item)' style="cursor:pointer">

                            </div>
                        </div>
                        <div class="list-content">
                            <p>{{item.comment_content}}</p>
                        </div>
                        <div class='list_time'>
                            <span class="header-time">回复时间：{{formatDateTime(myTime(item.post_time))}}</span>
                        </div>
                    </li>
                </ul>
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Comment",
        data(){
            return{
                commentList: [],
            }
        },
        created() {
            this.$axios.post('/api/comment/getCommentByUserId',{user_id: sessionStorage.getItem('user_id')})
                .then((response) => {
                    let res = response.data
                    if (res.status) {
                        this.$message.warning(res.msg)
                    } else {
                        this.commentList = [...res.result]
                    }
                })
        },
        methods: {
            goToBlog(comment) {
                this.$router.push({
                    path: '/content',
                    query: {
                        article_id: comment.article_id
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .Comment{
        min-height: 580px;
    }

     .blog-comment{
         width: 1160px !important;
         margin-left: 15px;
         margin-top: 60px;
     }
    span:nth-of-type(2){
        font-weight: normal!important;
        font-size: 12px !important;
        position: absolute;
        right: 40px;
        top: 25px;
        text-align: right;
    }
    img{
        width: 18px;
        position: absolute;
        top: 5px;
        right: 20px;
    }

    .comment-list{
        width: 100%;
        margin: 20px auto 10px;
        background-color: #e9eef3;
        padding: 10px;
    }
    .list_time span{
        position: absolute;
        bottom:2px;
        right: 15px;
        font-size: 12px;
    }
    .blog-comment .comment-list ul li {
        padding: 5px;
        width: 100%;
        position: relative;
        margin-bottom: 20px;
        border: 1px solid #ddd;
    }
    .blog-comment .comment-list ul .list-header {
        position: relative;
        margin-bottom: 10px;
        font-size: 14px;
    }
    .blog-comment img {
        border-radius: 3px;
        margin-right: 15px;
        display: inline-block;
        vertical-align: middle;
    }
    .header-reply span{
        font-weight: bold;
        position: absolute;
        left: 15px;
        font-size: 18px;

    }
    .blog-comment .comment-list ul .list-header .header-reply {
        display: inline-block;
        vertical-align: top;
        line-height: 1;
    }
    .blog-comment .comment-list ul .list-header .header-time {
        position: absolute;
        top: 12px;
        left: 0;
        font-size: 13px;
        color: #999;
    }
    .blog-comment .comment-list ul .list-content {
        margin-bottom: 10px;
    }
    ul {
        list-style: none;
    }

</style>